.contactsBlock {
    height: 100%;
    background-color: #212529;
    padding-bottom: 50px;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    font-family: "Poppins", Sans-serif;
}

.form {
    display: flex;
    flex-direction: column;
    justify-content:   space-between;
    align-items: center;
    height: 100%;
    width: 80%;
}

.back {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    color: rgba(255, 255, 255, .5);
    font-size: 2.25rem;
    opacity: 0.1;
    font-size: calc(1.95rem + 8.4vw);
    text-transform: uppercase;
    width: 100%;
    margin-top: 0;
    text-align: center;
}

.head {
    text-align: center;
    position: relative;
    display: flex;
    margin-bottom: -50px
}

.front{
    font-weight: 600;
    font-size: 2.25rem;
    color:white;
    align-self: center;
    width: 100%;
    position: absolute;
    margin-top: -50px;
    text-align: center;
}
.preForm{
    color:white;
}
.btn{
    background-color: #20c997;
    border-color: #20c997;
    padding: 0.8rem 2.6rem;
    font-weight: 500;
    border-width: 2px;
    transition: all 0.5s ease;
    border-radius: 50rem;
    color: white;
    margin-top: 30px;
}
input{
    border-color: #232a31;
    background: #232a31;
    color: #fff;
    padding: 0.81rem 0.96rem;
    height: inherit;
    font-size: 16px;
    display: block;
    width: 100%;
    font-weight: 400;
    line-height: 1.5;
    border: 1px solid #ced4da;
    appearance: none;
    border-radius: 0.25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    margin: 0;
    font-family: inherit;
    margin-top:1.5rem;
}
textarea{
    border-color: #232a31;
    background: #232a31;
    color: #fff;
    padding-top: 0.81rem;
    padding-right: 0.96rem;
    padding-bottom: 0.81rem;
    padding-left: 0.96rem;
    height: inherit;
    min-height: calc(1.5em + 0.75rem + 2px);
    font-size: 16px;
    display: block;
    width: 100%;
    font-weight: 400;
    line-height: 1.5;
    border: 1px solid #ced4da;
    appearance: none;
    border-radius: 0.25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    resize: vertical;
    margin-top:1.5rem;

}
@media screen and (max-width: 550px){
    .form {
        width: 100%;
    }
}